import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';

import { LoginCheckService } from 'src/app/api/controllers/LoginCheck';
import { UserService } from 'src/app/api/controllers/User';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  userEmailId:string = "";
  userPassword:string = "";

  userType:string = "";

  isInvalidPassword:boolean = false;
  isInvalidLogin:boolean = false;

  isSubmitted:boolean = true;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private loginCheckService: LoginCheckService,
    private apartmentService:ApartmentService,
    private userService: UserService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  clearErrors() {
  	
  }

  onInputChange(){
    this.isInvalidLogin = false;
    this.isInvalidPassword = false;
  }

  getUserDetails(id){

    let params = {
      userid: id
    }

    this.userService.getUserById(params).subscribe((res:any) => {
      

      var user = res[0];
      
      if(user.resetPassword)
      this.cookieService.set('isReset', 'yes');
      else
      this.cookieService.set('isReset', 'no');

      var userRoleType = this.cookieService.get('userRoleType');

      var params = {
        userId: id
      }

      this.apartmentService.getAllApartmentsByUserId(params).subscribe((res:any) => {
        
        this.cookieService.set('apartmentId', res[0].apartmentId);

        if(user.resetPassword){
          this.router.navigateByUrl('/profile/resetpassword/'+user.userId);
        }
        else if(userRoleType == 'Admin' || userRoleType == 'SuperAdmin' || userRoleType == 'Staff' || userRoleType == 'Security'){
          this.router.navigate(['/ams']);
        }
        else if(userRoleType == 'Owner' || userRoleType == 'Tenant') {
          this.router.navigateByUrl('/user');
        }
        else {
  
        }

        this.isSubmitted = true;

      },
      error => {
        console.log(error);
      });

    },
    error => {
      console.log(error);
    });


  }

  login() {

    this.isSubmitted = false;

    var params = {
      password: this.userPassword,
      emailId: this.userEmailId,
    }

    this.loginCheckService.authenticateUserByIdPassword(params).subscribe((res:any) => {
      
      if(res.errorMessage == undefined) {

        this.isInvalidLogin = false;
        this.isInvalidPassword = false;

        var data = res;

        //store the tooken in cookie
        this.cookieService.set('token', data.tokenString);
        this.cookieService.set('userId', data.userId);
        this.cookieService.set('userRoleType', data.roleTypeName);

        this.getUserDetails(data.userId);

      }
      else {
        this.isSubmitted = true;
        if(res.errorMessage.indexOf('password') != -1){
          this.isInvalidPassword = true;
          this.isInvalidLogin = false
        }
        else {
          this.isInvalidLogin = true
          this.isInvalidPassword = false
        }
        
        
      }
      


    },
    error => {
      this.isSubmitted = true;
    });
  }

  ngOnInit() {

    this.sharedService.clearCookies();

  }

}
